.opening {
    display: flex;
    flex-direction: column;
    justify-content: center;
  
    &__content {
      margin-top: 130px;
      text-align: center;
  
      @include mobile {
        margin-top: 100px;
      }
    }
  
    &__header {
      font-family: $font-raleway;
      font-size: 3.0625rem;
    }
  
    &__heading {
      margin-top: 20px;
      margin-bottom: 38px;
  
      @include mobile {
        padding: 0 70px;
      }
    }
  
    &__image {
      width: 100%;
      height: auto;
  
      @include mobile {
        width: 220%;
        transform: translateX(-28%);
      }
    }
  
    @include mobile {
      .btn {
        margin: 0 15px;
        padding: 16px 0;
        display: block;
      }
    }
  }
  
  